@font-face {
    font-family: 'MuseoSansRounded';
    src: url('museosansrounded-300-webfont.eot');
    src: url('museosansrounded-300-webfont.eot?#iefix') format('embedded-opentype'), url('museosansrounded-300-webfont.woff') format('woff'), url('museosansrounded-300-webfont.ttf') format('truetype'), url('museosansrounded-300-webfont.svg#museo_sans_rounded300') format('svg');
    font-weight: 300;
    font-style: normal;
}

